<template>
<div>  
 <!-- {{n}} -->  <!--加上{{}}，n才是一个变量-->
  <!--思考：n的值等于props: ["n"]里的"n"，对应了外面的n 所以 {{n}} 才会随着外面的n而变化-->
  <div class="cell" v-on:click="onClickSelf">
      <template v-if="a">{{text}}</template>
      <template v-else></template>
    </div>
  </div>
</template>
<script>
export default {
 props: ["n"],//props和外面的bind相对应使用 使用外面的n的值和变化
  data() {
    //es6
    return { a: false, /*text: "o"*/ text: "" }; //默认a 为false 点击后显示text的内容
  },
 /* computed: {  这是vue的计算属性
        text(){
            return this.n % 2 === 0 ? 'x' : 'o' 
        }
    },  这个是判断n到底是 x还是o 但是会有一个bug（点一下 所有的xx或oo一起变）所以弃用*/
  methods: { //对应上面的v-on：click
    onClickSelf() {
      if(this.text !== ""){  //防止重复点击的bug
        return
      }   
      this.a = true ; //对应onClickSelf里的a cell被点后a变成true
      this.text = this.n % 2 === 0 ? "x" : "o";
      this.$emit('click',this.text) ;//这个emit对应App.vue里的onClickCell 才知道cell被点了 a才会变成true
    }                // this.txet对应外面的$event
  }
};
</script>

<style>
.cell {
  border: 1px solid black;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 80px;
}
</style>
