<template>
  <div class="wrapper">
    
  <div>第 {{n}} 手</div>
  <div class='chess'>

    <div class="row">
      <Cell @click="onClickCell(0, $event)" v-bind:n="n" />
      <!-- n的值为n--->
      <Cell @click="onClickCell(1, $event)" :n="n" />
      <Cell @click="onClickCell(2, $event)" :n="n" />
    </div>
    <!--v-on:可简写成 @   v-bind都可以省略不写        -->

    <div class="row">
      <Cell @click="onClickCell(3, $event)" :n="n" />
      <Cell @click="onClickCell(4, $event)" :n="n" />
      <Cell @click="onClickCell(5, $event)" :n="n" />
    </div>
    <div class="row">
      <Cell @click="onClickCell(6, $event)" v-bind:n="n" />
      <Cell @click="onClickCell(7, $event)" v-bind:n="n" />
      <Cell @click="onClickCell(8, $event)" v-bind:n="n" />
    </div>
    <!--$event接收emit里的数据this.text（也可以是其他数据）-->
  </div>
  <!--<div>{{map}}</!--<div> -->
   <div>结果：{{result ===false? '胜负未分':`胜方为${result}`}} </div>
  </div>

</template>

<script>
import Cell from "./Cell"; //组件化 引用Cell.vue
export default {
  components: { Cell }, //es6语法 ☆啥意思

  data() {
    //☆看不懂 什么语法？
    //  return { n : 0 };
    return {
      n: 0,
      map: [[null, null, null], [null, null, null], [null, null, null]],
      result: false
    };
  },

  methods: {
    //里面装函数
    onClickCell(i, xx) {
      //函数名 和上面的v-on:click="onClickCell"相对应
      console.log(`${i}被点击了，内容是${xx}`); //需要在Cell.vue里加一个 $emit 不然就不会输出
      /*  i
      0 map[0][0]
      1 map[0][0]
      2 map[0][0]
      3 map[0][0]
      4 map[0][0]
      5 map[0][0]
      6 map[0][0]
      7 map[0][0]
      8 map[0][0]  */
      this.map[Math.floor(i / 3)][i % 3] = xx;

      this.n = this.n + 1; //xx这个名字随便取 反正就是接收$envent的值
      this.tell();
    },

    tell() {
      const map = this.map;
      for (let i = 0; i < 3; i++) {
        if (
          map[i][0] !== null &&
          map[i][0] === map[i][1] &&
          map[i][1] === map[i][2]
        ) {
          this.result = map[i][0];
        }
        for (let j = 0; j < 3; j++) {
          if (
            map[0][j] !== null &&
            map[0][j] === map[1][j] &&
            map[1][j] === map[2][j]
          ) {
            this.result = map[0][j];
          }

          if (
            map[0][0] !== null &&
            map[0][0] === map[1][1] &&
            map[2][2] === map[1][1]
          ) {
            this.result = map[0][0];
          }

          if (
            map[0][2] !== null &&
            map[0][2] === map[1][1] &&
            map[1][1] === map[2][0]
          ) {
            this.result = map[0][2];
          }
        }
      }
    }
  }
};
</script>

<style>
.row {
  display: flex;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
</style>
 